.playbar {
    z-index: 1;
    position: fixed;
    width: 100vw;
    min-height: 90px;
    background: #08090e;
    bottom: 0;
}

.song_controls {
    position: absolute;
    left: 20px;
    top: 10px;
    display: flex;
}

.song_control {
    padding: 15px;

}

.control {
    color: white;
    background-color: transparent;
    font-size: 35px;
}

.control:hover {
    color: #1987ff;
}

.playtime_slider {
    display: flex;
}

.playtime {
    color: white;
    position: relative;
    left: 220px;
    top: 30px;
    display: flex;
    font-size: 18px;
}

.slider {
    overflow: hidden;
    background-color: transparent;
    position: absolute;
    width: 99vw;
    height: 10px;
    left: 0.5vw;
    opacity: 0.2;
    outline: none;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 20px;
    background: #111420;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 20px;
    background: #111420;
    cursor: pointer;
}

.slider::-moz-range-progress {
    height: 100%;
    background: #1987ff;
}

.slider::-ms-fill-lower {
    height: 100%;
    background: #525359;
}

.song_info {
    position: absolute;
    width: 300px;
    height: 80px;
    display: grid;
    grid-column-gap: 20px;
    left: calc(50% - 150px);
    top: 10px;
    overflow: hidden;
    color: white;
}

.song_photo {
    padding: 0;
    height: 80px;
    width: 80px;
    grid-row: 1/3;
    grid-column: 1;
    border-radius: 5px;
    color: transparent;
}
.song_name {
    grid-row: 1;
    grid-column: 2;
    width: 220px;
    height: 30px;
}
.song_author {
    grid-row: 2;
    grid-column: 2;
    width: 220px;
    height: 30px;
    font-size: 13px;
    padding-left: 10px;
}

.tools {
    position: absolute;
    left: calc(50% + 100px);
    top: 20px;
    width: fit-content;
    color: white;
    font-size: 35px;
}

.tools span {
    transition: color 200ms ease-in-out;
    margin: 5px;
}

.tool_menu {
    position: absolute;
    top: -100px;
    right: -25px;
    border-radius: 5px;
    height: 100px;
    width: 100px;
    background-color: #16181d;
}

.clear_queue_button {
    position: relative;
    left: 5px;
    width: 90px;
    height: 30px;
    border-radius: 2px;
    background-color: #0a0d13;
    color: white;
    transition: background-color 200ms ease-in-out;
}

.clear_queue_button:hover {
    background-color: #0b223e;
}

.icon-heart:hover {
    color: red;
}

.icon-options-vertical:hover {
    color: #1987ff;
}

.closed {
    visibility: hidden;
}

.volume_controls {
    position: absolute;
    right: 20px;
    top: 35%;
}

.volume_controls > i {
    color: white;
    font-size: 25px;
}

.volume {
    margin-right: 10px;
    background-color: #0e1119;
}

.volume::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 20px;
    background: #111420;
}

.volume::-moz-range-thumb {
    width: 25px;
    height: 20px;
    background: #0b223e;
    cursor: pointer;
}

.volume::-moz-range-progress {
    height: 100%;
    background: #16181d;
}

.volume::-ms-fill-lower {
    height: 100%;
    background: #16181d;
}